import {GeneratorImpl} from './implementation/generator';
import {createLoader} from './loader';

/**
 * Main export for the library (async init)
 */
export default async () => {
  // Library jest teraz globalne (z Emscripten); Rollup wstrzyknie to automatycznie
  const {library} = await createLoader(Library);

  return {
    Generator: GeneratorImpl(library),
  };
};